.synth-container{
  /* border: 2px solid red;; */
  /* background-color: aquamarine; */
  display: -ms-flexbox;
  display: flex;
  text-align: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin: auto;
  height: 100%;
	font-family: 'Quattrocento', serif;
}

.particles-div {
	-ms-flex-line-pack: center;
	    align-content: center;
	width: 80%;
	margin: auto;
	/* margin-left: 20%;
	margin-right: 20%; */
	border: 5px solid white;
	/* padding-left: 20%; */
}

.note-button {
	-webkit-box-shadow:inset 0px -3px 7px 0px #fafafa;
	box-shadow:inset 0px -3px 7px 0px #fafafa;
	background-color:transparent;
	border-radius:3px;
	/* border:1px solid #000000; */
	display:inline-block;
	cursor:pointer;
	color:white;
	font-family: 'Quattrocento', serif;
	font-size:15px;
	padding:9px 23px;
	text-decoration:none;
	text-shadow:0px 1px 0px #263666;
}

.noteButton:hover {
	background-color:transparent;
}

.noteButton:active {
	position:relative;
	top:1px;
}

