.hourhand {
  width: 50%;
  height: 4px;
  background: white;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 100%;
      -ms-transform-origin: 100%;
          transform-origin: 100%;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transition-timing-function: cubic-beziercubic-bezier(0.52, 0.52, 1, 1);
       -o-transition-timing-function: cubic-beziercubic-bezier(0.52, 0.52, 1, 1);
          transition-timing-function: cubic-beziercubic-bezier(0.52, 0.52, 1, 1);
  /* animation-name: blur-box; */
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.minhand {
  width: 50%;
  height: 4px;
  background: white;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 100%;
      -ms-transform-origin: 100%;
          transform-origin: 100%;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: all 0.05s;
  -o-transition: all 0.05s;
  transition: all 0.05s;
  -webkit-transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
       -o-transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
          transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
  /* animation-name: blur-box; */
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.secondhand {
  width: 50%;
  height: 4px;
  background: black;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 100%;
      -ms-transform-origin: 100%;
          transform-origin: 100%;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: all 0.05s;
  -o-transition: all 0.05s;
  transition: all 0.05s;
  -webkit-transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
       -o-transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
          transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
  /* animation-name: blur-box; */
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes blur-text {
  from { text-shadow: 10px 2px 30px white;
  }

  to { text-shadow: 10px 2px 5px white;
  }
}

@keyframes blur-text {
  from { text-shadow: 10px 2px 30px white;
  }

  to { text-shadow: 10px 2px 5px white;
  }
}

@-webkit-keyframes blur-box {
  from { -webkit-box-shadow: 30px 20px -20px rgba(255,255,255,1); box-shadow: 30px 20px -20px rgba(255,255,255,1);
  }

  to { -webkit-box-shadow: 0px 0px 44px 28px rgba(177, 174, 174, 0.555); box-shadow: 0px 0px 44px 28px rgba(177, 174, 174, 0.555);
  }
}

@keyframes blur-box {
  from { -webkit-box-shadow: 30px 20px -20px rgba(255,255,255,1); box-shadow: 30px 20px -20px rgba(255,255,255,1);
  }

  to { -webkit-box-shadow: 0px 0px 44px 28px rgba(177, 174, 174, 0.555); box-shadow: 0px 0px 44px 28px rgba(177, 174, 174, 0.555);
  }
}


